  font-family: 'DM Serif Display';
  src: url(${___CSS_LOADER_URL_REPLACEMENT_0___}) format('truetype');
}

@font-face {
  font-family: 'Inter';
  src: url(${___CSS_LOADER_URL_REPLACEMENT_1___}) format('truetype');
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
}
input,
button,
textarea,
select {
  font: inherit;
}
p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
}
#root,
#__next {
  isolation: isolate;
}



:root {
  --coffee-card: #967259;
  --coffee-text: #ece0d1;
  --heading-color: #38220f;
  --misc-color: #634832;
  --paragraph-color: #272727e0;
  --btn-active: #fff5e9;
  --btn-inactive: #fff5e9be;
}

body {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_2___});
  background-size: cover;
  background-position: center;
  background-attachment: fixed;

  font-family: 'Inter', system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-size: 1.3rem;

  display: grid;
  grid-template-columns: 1fr 2fr 1fr;
  grid-template-rows: 0.2fr 5fr 0.6fr;
}

header {
  background-color: var(--heading-color);

  grid-column: 1/4;
  grid-row: 1/2;

  height: 70px;
}
nav {
  display: flex;
  justify-content: center;

  gap: 7vw;
}
nav button {
  background-color: transparent;
  border: 0;

  padding: 0.5em 0.5em 0.1em 0.5em;
  margin-bottom: 0.5em;

  font-family: 'DM Serif Display', 'Times New Roman', Times, serif;
  font-size: 1.5rem;
}
nav button:hover {
  cursor: pointer;
}

.btn-active {
  color: var(--btn-active);
  border-bottom: 2px solid var(--btn-active);
}
.btn-inactive {
  color: var(--btn-inactive);
  border: 0;
}

#content {
  grid-column: 2/3;
  grid-row: 2/4;

  background-color: rgb(255, 248, 225);

  margin: 1em 0;
  border-radius: 15px;
  border: 5px solid var(--misc-color);
}

/* Home */
.heading h1, .heading p {
  text-align: center;
}
.heading h1 {
  font-family: 'DM Serif Display', 'Times New Roman', Times, serif;
  text-align: center;
  font-size: min(5vw, 4rem);
  color: var(--heading-color)
}
.heading p {
  font-weight: 500;
  color: var(--heading-color)
}

.home p {
  margin: 0.5em 0;
}

.home p, .home-list {
  line-height: 1.5;
  text-indent: 15px;
  font-weight: 420;

  text-align: justify;
  
  color: var(--paragraph-color);
}

.home {
  padding: 1em 1em;
}
#after-list {
  margin: 2em 0;
}

.home-list {
  list-style: none;
  text-indent: 0px;

  display: flex;
  flex-direction: column;
  justify-content: space-between;

  gap: 0.5em;
}

#home-final {
  display: flex;
  justify-content: center;

  font-weight: 500;
  text-indent: 0px;
  color: var(--heading-color);
}

.location {
  padding: 1em;
}
.location h2 {
  font-family: 'DM Serif Display', 'Times New Roman', Times, serif;
  text-align: center;
  color: var(--heading-color)
}

.location address {
  font-style: normal;
  display: flex;
  flex-direction: column;
  align-items: center;

  text-align: center;

  color: var(--paragraph-color);
}
.location .phone {
 text-decoration: none;
 color: var(--heading-color);
 font-weight: 550;
}

/* Menu */
.menu-container {
  display: flex;
  flex-direction: column;
  gap: 1.5em;
}

.coffee-card {
  display: flex;
  align-items: center;
  flex: 1;
  gap: 2em;

  border: 5px solid var(--coffee-card);
  border-radius: 15px;
  background-color: #38220f;
  color: var(--coffee-text);

  margin: 1em 2em;
  padding-bottom: 1em;

  box-shadow: #666666 5px 5px 10px;
}
.coffee-card img {
  width: 200px;
}
.coffee-card .info {
  padding-right: 1em;
}
.coffee-card .info .name {
  font-family: 'DM Serif Display', 'Times New Roman', Times, serif;
  font-size: min(5vw, 4rem); 
}
.coffee-card .info .desc {
  font-weight: 500;
}
.coffee-card .info .price {
  padding-top: 0.5em;
}

/* About */

footer {
  background-color: var(--heading-color);
  grid-column: 1/4;
  grid-row: -1;

  display: flex;
  flex-direction: column;
  align-items: center;

  font-size: smaller;
  color: #ffffff9d;
}

footer a {
  color: #967259;
  text-decoration: none;
